@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Onest:wght@200;400;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  text-decoration: none;
  font-size: 12px;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

img {
  display: block;
}

button {
  box-sizing: border-box;
  min-width: 44px;
  min-height: 44px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}
